<template>
  <div id="cesiumContainer"></div>
</template>

<script>
import 'cesium/Build/Cesium/Widgets/widgets.css'
import * as Cesium from 'cesium'

export default {
  data() {
    return {
      selectedFile: null,
      viewer: null,
    };
  },
  mounted() {
    this.init()
  },
  methods: {
    async init() {
      const key = "cec5eeadc16a3c964451ff083266df0c"
      const viewer = new Cesium.Viewer('cesiumContainer', {
        animation: false,
        fullscreenButton: false,
        geocoder: false,
        homeButton: false,
        infoBox: false,
        sceneModePicker: false,
        selectionIndicator: false,
        timeline: false,
        navigationHelpButton: false,
        navigationInstructionsInitiallyVisible: false,
        imageryProvider: new Cesium.WebMapTileServiceImageryProvider({
          url: "http://t0.tianditu.gov.cn/vec_w/wmts?tk=" + key,
          layer: "vec",
          style: "default",
          tileMatrixSetID: "w",
          format: "tiles",
          maximumLevel: 18,
        })
      })
      //加载3D模型
      const tileset = await Cesium.Cesium3DTileset.fromIonAssetId(2489232);
      viewer.scene.primitives.add(tileset);

      if (Cesium.FeatureDetection.supportsImageRenderingPixelated()) {
        //判断是否支持图像渲染像素化处理
        viewer.resolutionScale = window.devicePixelRatio
      }
      viewer.scene.postProcessStages.fxaa.enabled = true

      // 暗色系
      // viewer.imageryLayers.get(0).hue = 3
      // viewer.imageryLayers.get(0).contrast = -1.2
      viewer.imageryLayers.addImageryProvider(new Cesium.WebMapTileServiceImageryProvider({
        url: "http://t{s}.tianditu.gov.cn/ibo_w/wmts?tk=" + key,
        layer: "ibo",
        style: "default",
        tileMatrixSetID: "w",
        format: "tiles",
        maximumLevel: 18,
        subdomains: ['0', '1', '2', '3', '4', '5', '6', '7']
      }))
      viewer.imageryLayers.addImageryProvider(new Cesium.WebMapTileServiceImageryProvider({
        url: "http://t0.tianditu.gov.cn/cva_w/wmts?tk=" + key,
        layer: "cva",
        style: "default",
        tileMatrixSetID: "w",
        format: "tiles",
        maximumLevel: 18
      }))

      // 将三维球定位到中国
      // 获取模型的包围盒
      const boundingSphere = tileset.boundingSphere;
      // 将摄像头飞到模型位置
      viewer.camera.flyToBoundingSphere(boundingSphere, {
        duration: 0
      });
    },
  },
}
</script>

<style lang="scss" scoped>
#cesiumContainer {
  width: 100%;
  height: 100%;
  position: relative;
}
</style>
